import { observer } from 'mobx-react-lite'
import React from 'react'
import { InfoElement, Title, Wrapper } from './InfoBar.styles'
import InfoWindow from '../InfoWindow/InfoWindow'

const InfoBar = observer(() => {
    return (
        <Wrapper>
            <Title>:</Title>
            <InfoWindow />
            <InfoElement style={{ textAlign: 'center', width: '80%', margin: 0 }}>  </InfoElement>
            <InfoElement style={{ textAlign: 'center', width: '80%' }}>(   )</InfoElement>
            <InfoElement style={{ color: '#ff7675', fontWeight: 'bold', marginBottom: '0px' }}>  ( )</InfoElement>
            <InfoElement>0    100 </InfoElement>
            <InfoElement style={{ color: '#fdcb6e', fontWeight: 'bold', marginBottom: '0px' }}>  ()</InfoElement>
            <InfoElement>100 {`<`}   500 </InfoElement>
            <InfoElement style={{ color: '#2ecc71', fontWeight: 'bold', marginBottom: '0px' }}>  ( )</InfoElement>
            <InfoElement>500 {`<`}   1000 </InfoElement>
            <InfoElement style={{ color: '#74b9ff', fontWeight: 'bold', marginBottom: '0px' }}>  (  )</InfoElement>
            <InfoElement>X {`>`} 1000 </InfoElement>
        </Wrapper>
    )
})

export default InfoBar
